/* FONTS */ /* GENERAL */
* {
  box-sizing: border-box;
  font-family: "Hanken Grotesk", sans-serif;
}

*::before,
*::after {
  box-sizing: inherit;
}

html {
  height: 100%;
  box-sizing: border-box;
}

img {
  display: block;
  height: auto;
}

body {
  display: flex;
  flex-direction: column;
