/* VISUALLY-HIDDEN */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* CONTAINER */
.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 20px;
}

.header {
  width: 100%;
  height: 6rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header .header_container {
  width: 70%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: nowrap;
}
.header .header_container .logo__box {
  width: 17%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.header .header_container .logo__box img {
  width: 100%;
  border-right: 0.7px solid #aaa;
  padding: 0px 40px 0 0;
}
.header .header_container .navbar {
  width: 40%;
  display: flex;
  justify-content: space-between;
  list-style: none;
}
.header .header_container .navbar a {
  text-decoration: none;
  color: gray;
  font-weight: 500;
  font-size: 18px;
  transition: 0.3s all linear;
  border-bottom: 0.3px solid transparent;
}
.header .header_container .navbar a:hover {
  color: rgb(177, 177, 177);
  border-bottom: 0.3px solid rgb(177, 177, 177);
}
.header .header_container .quote {
  width: 20%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.header .header_container .quote a {
  text-decoration: none;
  padding: 10px 30px;
  background-color: rgba(71, 191, 111, 0.1019607843);
  color: #31a357;
  border-radius: 6px;
  transition: 0.4s all linear;
  font-size: 20px;
}
.header .header_container .quote a:hover {
  color: #fff;
  background-color: #31a357;
}
.header .header_container .active a {
  color: #fff;
  background-color: #31a357;
}
.header .header_container .colapse_menu {
  display: none;
}

