:root {
  --default-height: 29rem;
  --default-width: 35%;
  --level2-height: 20.58rem;
  --level2-width: --default-width - 5%;
  --level1-height: 24.34rem;
  --level1-width: --default-width - 5%;
  --level0-height: 29.95rem;
  --level0-width: --default-width + 5%;
  --level-2-left: 15%;
  --level-1-left: 20%;
  --level0-left: 50%;
  --level1-left: 75%;
  --level2-left: 85%;
  --level-2-bs: -0.1em 0.1em 0.2em #212121;
  --level-1-bs: -0.2em 0.2em 0.4em #212121;
  --level2-bs: 0.1em 0.1em 0.2em #212121;
  --level1-bs: 0.2em 0.2em 0.4em #212121;
  --level0-bs: 0 0.4em 0.8em #212121;
  --height: 19rem;
}

