.arrow {
  position: absolute;
  width: 60px;
  height: 60px;
  text-align: center;
  border: 1px solid #673ab7;
  font-size: 2em;
  cursor: pointer;
  line-height: 30px;
  z-index: 1000;
  top: 50%;
  transform: translate(0, -50%);
  border-radius: 50%;
  transition: all 500ms;
}
.arrow i {
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  position: absolute;
  color: #512da8;
}
.arrow-right {
  right: 10%;
}
.arrow-left {
  left: 10%;
}
.arrow:hover {
  background-color: #512da8;
  color: #ffeb3b;
}
.arrow:hover i {
  color: #fff;
}

